.search {
 
  transform: translateY(-80%);
  height: 80px;
  background: $color-white-0;
  // background-color: $color-red-primary;
  // background-color: $color-black-1;
  border-radius: 5px;
  display: none;
  @include RESPONSIVE("992px") {
    display: block;
  }
  
  &__form {
    color: $color-couple;
    display: flex;
    height: 100%;
    // border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
  }
  &__group {
  //  background-image: linear-gradient(to top ,rgb(0,0,0) 0%,rgba(0,0,0,0) 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: calc(70% / 4);
    // border-radius: 5px;

    select {
      // background-image: linear-gradient(to top ,rgb(0,0,0) 0%,rgba(0,0,0,0) 100%);
      appearance: none;
      width: 100%;
      height: 100%;
      cursor: pointer;

      border: none;
      outline: none;
      padding: $sp-20;
      border-radius: 5px

      option {
        border: 1px solid #e4e4e4;
      }
    }

    &::after {
      content: "";
      @include positionAbsolute(50%, false, false, 0);
      transform: translateY(-50%);
      width: 2px;
      height: 50%;
      background-color: #e4e4e4;
    }
    &::before {
      content: "";
      @include positionAbsolute(50%, false, false, 10px);
      transform: translateY(-50%);
      background: url("/images/dropdown-icon.png") center center/contain
        no-repeat;
      width: 15px;
      height: 15px;
      cursor: pointer;
      pointer-events: none;
    }
  }

  .movieSelect {
    width: 30%;
  }

  &__button {
    .btnBuyTicket {
      width: unset;
      height: unset;
      padding: 10px;
      font-size: 14px;
      &:not(:disabled):hover {
        font-size: 14px !important;
      }
    }

    &::before,
    &::after {
      display: none;
    }
  }
}
