#pay-section {
  //@include childGrid(4, 5, 1, 3);
  background: $color-white-0;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  z-index: 98;
  position: relative;

  padding: 0;
  width: 100%;
  height: 100vh;

  @include RESPONSIVE("768px") {
    position: fixed;
    top: 0;
    right: 0;
  }
  .pay-wrapper {
    // @include positionAbsolute(0,0);
    // position: relative; 

    // width: 100%;
    height: 100%;
    padding: 0 $sp-10 100px;
    overflow-y: scroll;
    overflow-x: hidden;
    // height: 100vh;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  .pay__item--wrapper {
    padding: $sp-20 0;
    border-bottom: 1px dashed #ccc;
    min-height: 40px;
    // @include displayFlex(space-between, center);
    .row [class*="col-"] {
      padding: 0;
    }
    &.combo-click{
      cursor: pointer;
    }
  }
  .total {
    @include styleText($color-choosing, $fs-32);
    text-align: center;
    height: 40px; //bằng với process
    span {
      display: inline-block;
      line-height: 40px;
      font-weight: 700;
    }
  }
  .seatchosen {
    .myseat {
      @include styleText($color-red-primary, $fs-14);
      word-break: break-all;
    }
    .demoMoney {
      @include styleText($color-choosing, $fs-16);
    }
  }
