  .howtopay {
    .radio-selection {
      .radio__item {
        margin-bottom: $sp-10;
        cursor: pointer;
        @include displayFlex(flex-start, center);
      }
      .radio__item--input[type="radio"] {
        cursor: pointer;
        background-color: #fff;
        -webkit-appearance: none;
        width: 20px;
        height: 20px;
        border: 2px solid #4a90e2;
        border-radius: 50%;
        outline: 0;
        position: relative;

        &::before {
          content: "";
          display: block;
          @include positionAbsolute(50%, 50%);
          transform: translate(-50%, -50%);
          width: 50%;
          height: 50%;
          border-radius: 50%;
        }

        &:checked::before {
          background: #4a90e2;
        }
      }
      .radio__item--label {
        flex: 1;
        cursor: pointer;
        @include displayFlex(flex-start, center);
        margin: 0;

        .pay__figure {
          width: 40px;
          height: 40px;
          margin-right: $sp-10;
          margin-left: $sp-10;
        }

        .pay__text {
          @include styleText($color-black-1, $fs-14);
          margin: 0;
        }
      }
    }
  }
  .confirm__group {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    //.confirm__item
  }
